<template>
<div>
        <link rel="stylesheet" href="static/css/newuser.css">
    <link rel="stylesheet" href="static/css/style.css">
    <div class="container user_box">
        <div class="panel panel-info">
            <div class="panel-heading">
                <h3 class="panel-title"></h3>
            </div>
            <div class="panel-body">
                <div class="col-sm-12">
                    <a class="btn btn-link  pull-right" id="index" href="#/"></a>
                </div>
                <form  class="form form-horizontal col-sm-4 col-sm-offset-4" name="myForm" :action="HOST+'register.action'" method="post" ref="myForm">
                    <div class="form-group"  v-html="errormsg"></div>
                    <div class="form-group" :class="{'has-success':!errors.has('tel')&&newuser.tel!='','has-error':errors.has('tel')}">
                        <div class="col-sm-12" >
                            <input type="text" name="tel" v-model="newuser.tel" placeholder="11" data-vv-as="" class="form-control" v-validate="'required|tel'" @input="removecodeMSg"></div>
                        <div class="col-sm-11 col-sm-offset-1 text-alert text-danger">
                           <span v-show="errors.has('tel')">{{ errors.first('tel') }}</span>
                           <span v-html="codeerrormsg"></span>
                        </div>
                        
                    </div>
                    <div class="form-group" :class="{'has-success':!errors.has('code')&&newuser.code!='','has-error':errors.has('code')}">
                        <div class="col-sm-7">
                            <input type="text" name="code" data-vv-as="" v-model="newuser.code" placeholder="" class="form-control" v-validate="'required|numeric|length:6'"
                                :disabled="errors.has('tel')||newuser.tel==''">
                        </div>
                        <div class="col-sm-5"><button type="button"  class="btn btn-primary btn-block" @click="loginCode" :disabled="errors.has('tel')||newuser.tel==''||codedis" ref="code" v-html="codeinner"></button></div>
                        <div class="col-sm-11 col-sm-offset-1 text-alert text-danger">
                            <span v-show="errors.has('code')">{{ errors.first('code')}}</span>
                        </div>
                    </div>
                    <div class="form-group" :class="{'has-success':!errors.has('userid')&&newuser.userid!='','has-error':errors.has('userid')}">
                        <div class="col-sm-12">
                            <input type="text" name="userid" data-vv-as="" v-model="newuser.userid" placeholder="" class="form-control"  v-validate="'required|cnlength:6'"></div>
                        <div class="col-sm-11 col-sm-offset-1 text-alert text-danger">
                            <span v-show="errors.has('userid')">{{ errors.first('userid')}}</span>
                        </div>
                    </div>
                    <div class="form-group" :class="{'has-success':!errors.has('password')&&newuser.pass!='','has-error':errors.has('password')}">
                        <div class="col-sm-12">
                            <input type="password" name="password" data-vv-as="" v-model="newuser.pass" placeholder="" class="form-control"  v-validate="'required|password'">
                            </div>
                        <div class="col-sm-11 col-sm-offset-1 text-alert text-danger">
                             <span v-show="errors.has('password')">{{ errors.first('password')}}</span>
                        </div>
                        <div class="col-sm-12" style="font-size:8px;">6-16</div>
                    </div>
                    <div class="form-group" :class="{'has-success':!errors.has('repw')&&newuser.pass==newuser.password&&newuser.password!='','has-error':errors.has('repw')||newuser.pass!=newuser.password}">
                        <div class="col-sm-12">
                            <input type="password" name="repw" data-vv-as="" v-model="newuser.password" placeholder="" class="form-control"  v-validate="'required'"></div>
                        <div class="col-sm-11 col-sm-offset-1 text-alert text-danger">
                            <span v-show="errors.has('repw')">{{ errors.first('repw')}}</span>
                            <span v-show="newuser.pass!=newuser.password&&newuser.password!=''"></span>
                        </div>
                    </div>
                    <div class="form-group">
                        <button type="button" class="btn btn-info col-sm-5 col-sm-offset-3" @click="submits"></button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</div>
</template>
<script>
export default {
  data: () => {
    return {
      newuser: {
        tel: "",
        code: "",
        userid: "",
        pass: "",
        password: ""
      },
      codedis: false,
      codeinner: "",
      codeerrormsg: "",
      errormsg: ""
    };
  },
  methods: {
    removecodeMSg() {
      var vm = this;
      vm.codeerrormsg = "";
    },
    loginCode() {
      var vm = this;
      var tel = vm.newuser.tel;
      if (vm.codeerrormsg) {
        return;
      } else {
        vm.$axios
          .post(vm.HOST + "sendCode.action", { tel: tel })
          .then(function(response) {
            var res = response.data;
            if (res.code == "0") {
              vm.codeDis();
              vm.$layer.alert(
                '<div class="layer divtext"><i class="icon icon-success"></i></div>'
              );
            } else {
              var msg = res.msg;
              const bool = msg == "," ? true : false;
              msg = bool ? msg + "<a href='#/'></a>" : msg;
              vm.codeerrormsg = msg;
            }
          })
          .catch(function(error) {
            vm.$layer.alert(
              '<div class="layer divtext"><i class="icon icon-warning"></i></div>'
            );
          });
      }
    },
    codeDis() {
      var vm = this;
      vm.codedis = true;
      var i = 10;
      upText();
      let times = setInterval(upText, 1000);
      function upText() {
        vm.codeinner = i + "";
        i -= 1;
        if (i <= -1) {
          clearInterval(times);
          vm.codeinner = "";
          vm.codedis = false;
        }
      }
    },
    submits() {
      var vm = this;
      var newuser = vm.newuser;
      vm.$validator.validateAll().then(function(value) {
        if (value && newuser.pass == newuser.password) {
          vm.$refs.myForm.submit();
        }
      });
    }
  }
};
</script>




